.thumbnail-component {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 0 100px inset #bfa570, 0 0 5px #b89d67;
  transition: box-shadow 1s;
  margin-left: 10px;
  padding: auto; }
  .thumbnail-component .delete-checkbox {
    position: flex;
    margin-top: 0px;
    margin-right: 185px;
    appearance: none;
    width: 15px;
    height: 15px;
    background-color: #fff;
    border: 2px solid #999;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s, border-color 0.2s; }
  .thumbnail-component .delete-checkbox:checked {
    background-color: #7a5e26;
    border-color: #bfa570; }
  .thumbnail-component:after {
    width: 80%;
    height: 80%;
    display: block;
    white-space: pre;
    font: 15pt 'sigmar one';
    color: #bfa570;
    border: 2px solid;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 0.1s 0.9s;
    overflow: hidden;
    text-overflow: ellipsis; }
  .thumbnail-component:hover {
    box-shadow: 0 0 0 2px inset #b89d67, 0 0 5px #997e46;
    transition: box-shadow 1s; }
  .thumbnail-component:hover::after {
    opacity: 0;
    transition: opacity .5s; }
  .thumbnail-component div {
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center; }
  .thumbnail-component p {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 9px 0 0;
    font-size: 15px;
    text-align: center;
    white-space: pre-wrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 500px;
    /* Add max-width */ }
  .thumbnail-component .check {
    margin-right: auto; }
  .thumbnail-component span {
    font-size: 15px;
    padding-right: 2px; }
