.Header-component {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f1e8ce;
  border: none;
  border-bottom: 2px solid #000000;
  margin: 10px 0; }
  .Header-component button::before,
  .Header-component button::after {
    box-sizing: border-box; }
  .Header-component button {
    position: relative;
    display: inline-block;
    cursor: pointer;
    outline: none;
    border: 0;
    vertical-align: middle;
    text-decoration: none;
    font-size: 1.1rem;
    color: #7a5e26;
    font-weight: 700;
    text-transform: uppercase;
    font-family: inherit;
    margin-right: 10px;
    padding: 1em 2em;
    border: 2px solid #7a5e26;
    border-radius: 1em;
    background: #eadebb;
    transform-style: preserve-3d;
    transition: all 175ms cubic-bezier(0, 0, 1, 1); }
    .Header-component button::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: #eadebb;
      border-radius: inherit;
      box-shadow: 0 0 0 2px #997e46, 0 0.75em 0 0 #7a5e26;
      transform: translate3d(0, 0.75em, -1em);
      transition: all 175ms cubic-bezier(0, 0, 1, 1); }
    .Header-component button:hover {
      background: #f1e8ce;
      transform: translate(0, 0.375em); }
      .Header-component button:hover::before {
        transform: translate3d(0, 0, -1em);
        box-shadow: 0 0 0 2px #997e46, 0 0.25em 0 0 #997e46; }
    .Header-component button:active {
      transform: translate(0em, 0.75em); }
  .Header-component .Logo {
    width: 100px;
    height: 100px;
    transition: 1s ease; }
    .Header-component .Logo:hover {
      -webkit-transform: scale(1.5);
      -ms-transform: scale(1.5);
      transform: scale(1.5);
      transition: 1s ease; }
  .Header-component .Header-title {
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px; }
    .Header-component .Header-title h1 {
      font-size: 40px;
      font-weight: bold;
      margin-right: 10px;
      letter-spacing: 1px;
      white-space: nowrap;
      color: #7a5e26; }
  @media (min-width: 768px) {
    .Header-component {
      flex-direction: row;
      justify-content: space-between; }
      .Header-component.Header-title {
        margin-bottom: 0; }
      .Header-component button {
        justify-content: flex-end;
        margin-left: auto; } }
