form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-family: Arial, sans-serif;
  font-size: 10px;
  background-color: #dcc595;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  max-height: fit-content;
  max-width: 400px;
  margin-top: 2px;
  margin-left: 20px;
  margin-bottom: 1rem;
  overflow: auto; }

label {
  display: inline-block;
  color: #5a4405;
  font-size: medium;
  width: 50px;
  margin-right: 0.5rem;
  margin-bottom: 1rem; }

input[type="text"] {
  display: inline-block;
  background-color: #f6f1e3;
  color: #675317;
  font-size: medium;
  padding: 0.5rem;
  border-radius: 4px;
  border: 2px solid #c4b179;
  max-width: 300px;
  box-sizing: border-box;
  margin-left: 1rem;
  margin-bottom: 1rem; }

select {
  display: inline-block;
  background-color: #f6f1e3;
  color: #675317;
  font-size: medium;
  padding: 0.5rem;
  border-radius: 4px;
  border: 2px solid #c4b179;
  max-width: 300px;
  box-sizing: border-box;
  margin-left: 1rem;
  margin-bottom: 1rem; }

.popup {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #f1e8ce;
  text-transform: uppercase;
  color: #6d5200;
  padding: 20px;
  border: 2px solid #ccc;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  z-index: 9999; }

.form p {
  color: #4b3a06;
  font-size: medium; }
